






















import {Vue, Component, Prop, Watch} from "vue-property-decorator";

import {Button} from 'iview';

@Component({
    components: {Button}
})

export default class typeItemChartList extends Vue {
    @Prop({
        default: function () {
            return {
                name:'项目',
                count:12,
                per:'100%',
            }
        }
    })
    chartList!: Array<any>;
    @Prop({
        default: false,
    })
    both?: boolean;
    @Prop({
        default: '',
    })
    unit?: string;
    @Prop({
        default: false,
    })
    rank?: boolean;
    max:number = 0;

    @Watch(`chartList`)
    listchange(){
        this.max = 0;
        for(let i=0;i<this.chartList.length;i++){
            if(this.chartList[i].count > this.max){
                this.max = this.chartList[i].count
            }
            this.chartList[i].per = `0%`
        }

        setTimeout(()=>{
            this.chartList.forEach(item =>{
                let tw:any = item.count/this.max*100;
                if(tw < 2){
                    tw = 2
                }
                item.per = `${tw}%`;
            })
        },1000)
    }

    calcWidth(per:string){
        if(per){
            return `width:${per}`
        }
        else{
            return `width:2%`
        }
    }


    mounted() {
        this.max = 0;
        for(let i=0;i<this.chartList.length;i++){
            if(this.chartList[i].count > this.max){
                this.max = this.chartList[i].count
            }
            this.chartList[i].per = `0%`
        }

        setTimeout(()=>{
            this.chartList.forEach(item =>{
                let tw:any = item.count/this.max*100;
                if(tw<2){
                    tw = 2
                }
                item.per = `${tw}%`;
            })
        },1000)
    }
}
